<template>
	<div class="personalInfo-page">
		<div class="personalInfo-header">
			<div class="personalInfo-header-back" @click="$router.back()">
				<i class="fa fa-angle-left fa-2x"></i>
				<span>{{$t('pageCommon.back')}}</span>
			</div>
		</div>
		<el-table
			:data="tableData"
			stripe
			class='personalInfo-table'>
			<el-table-column
				prop="loginName"
				label="登录名"
				width="104">
			</el-table-column>
			<el-table-column
				prop="userName"
				label="用户名">
			</el-table-column>			
			<el-table-column
				prop="role"
				label="角色">
			</el-table-column>
			<el-table-column
				prop="phone"
				label="电话">
			</el-table-column>			
            <el-table-column
                label="管理"
                width="160">
                <template scope="scope">
                    <!--<el-button class='personalInfo-table-editor'>{{$t('pageCommon.editorText')}}</el-button>
					<el-button class='personalInfo-table-delete'>{{$t('pageCommon.deleteText')}}</el-button>-->
                </template>
            </el-table-column>
		</el-table>
		<el-pagination
			mini
			layout="prev, pager, next"
			:total="50" class='personalInfo-pagination'>
		</el-pagination>
	</div>
</template>

<script>
	import DirList from '../packageJS/dirList.js'
	import './personalInfo.scss'
    export default {
		data(){
			return {				
				tableData : []
			}
		},
		created(){
			let userInfo = this.$http.GetCurrentUserInfo()
			let role = this.filtersRole(userInfo.role)
			this.tableData = [
				{
					loginName : userInfo.name,
					userName : userInfo.info.name,
					role : role,
					phone : userInfo.phone
				}
			]
		},
		methods : {
			filtersRole(value = '') {
				switch (value) {
					case 'admin':
						value = '管理员'
						break;
					case 'aide':
						value = '秘书'
						break;
					default:
						value = '普通用户'
						break;
				}
				return value
			}
		}
    }	
</script>


