<template>
    <div class="addTopic">
        <div class="addTopic-header">
            <p @click="$router.back(-1)"><i class="fa fa-angle-left fa-2x"></i><span>{{$t('pageCommon.back')}}</span></p>
            <div>
                <el-button @click="TopicHandle">{{$t('addTopic.save')}}</el-button>
            </div>
        </div>
        <div class="topic-box" ref="allMsg">
            <div class="addTopicTable table-responsive">
                <div class="header-topic">
                    <h5>{{$t('addTopic.tip')}}</h5>
                </div>
                <table class="table">
                    <tbody>
                        <tr class="row">
                            <th class="col-md-2">{{$t('addTopic.topic')}}</th>
                            <td colspan="5" class="col-md-12"><input type="text" v-model="addTopicInfo.title"></td>
                        </tr>
                        <tr class="row">
                             <th class="col-md-2">{{$t('addTopic.type')}}</th>
                            <td class="col-md-2">
                                 <select v-model="addTopicInfo.type" @change="allMsgScrollTop">
                                    <option value=""></option>
                                    <option value="学习类议题">{{$t('addTopic.typeTd.vote')}}</option>
                                    <option value="通报类议题">{{$t('addTopic.typeTd.report')}}</option>
                                    <option value="研究类议题">{{$t('addTopic.typeTd.research')}}</option>
                                </select> 
                            </td>
                            <th class="col-md-2">{{$t('addTopic.department')}}</th>
                            <td class="col-md-2">
                                <select v-if="officeInfo.length" v-model="addTopicInfo.department" @change="getSupervisor">
                                    <option value=""></option>
                                    <option v-for="info in officeInfo" :value="info.name">{{info.name}}</option>
                                </select> 
                            </td>
                            <th class="col-md-2">{{$t('addTopic.principal')}}</th>
                            <td class="col-md-2">
                                <!-- <select v-model="addTopicInfo.supervisor" v-if="userMsg.length">
                                    <option value=""></option>
                                    <option v-for="data in userMsg" :value="data.name">{{data.name}}</option>
                                </select>  -->
    
                                <!-- 临时 -->
                                <select  v-model="addTopicInfo.supervisor">
                                    <option value=""></option>
                                    <option v-for="users in officeUsers" :value="users.child.name">{{users.child.name}}</option>
                                </select>
                            </td>
                        </tr>
                        <!-- ================收件人============== -->
                        <tr class="row">
                            <th class="col-md-2">{{$t('addTopic.receiver')}}</th>
                            <td colspan="5" class="col-md-10">
                                <el-tag type="primary">
                                    {{publisherInfo.name}}
                                </el-tag>
                                <!-- <span class="addTopic-add" @click="$refs.userData.GetPersonnelList(), $store.dispatch('personnelList')"><i class="fa fa-plus fa-lg"></i>{{$t('addTopic.selectStaff')}}</span> -->
                            </td>
                        </tr>
                        <!-- ================富文本编辑器=========== -->
                        <tr class="row">
                            <th rowspan="2" class="col-md-2">{{$t('addTopic.content')}}</th>
                            <td colspan="5" class="col-md-10" style="padding:0;">
                                <quill-editor v-model="addTopicInfo.content" :options="editorOption"></quill-editor>
                            </td>
                        </tr>
                        <tr>
                        </tr>
                        <!-- ================上传附件=========== -->
                        <tr class="row">
                            <th class="col-md-2">
                                {{$t('addRecord.upload')}}
                                <p class="ps">只允许上传以下格式</p>
                                <p class="ps">(pdf、xls、xlsx、doc、ppt、pptx、docx、azw3)</p>
                            </th>
                            <td  colspan="5" class="col-md-10">
                                    <el-tag type="primary"
                                     v-for="(file, index) in addTopicInfo.attachments"
                                    :key="file._id"
                                    :closable="true"
                                    :close-transition="false"
                                    @close="delFile(addTopicInfo.attachments, index)">
                                        {{file.originalname}}
                                    </el-tag>
                                    <i class="fa fa-paperclip fa-lg"></i>
                                    <vue-file-upload
                                        url='/api/attachments/uploaderFile'
                                        ref="vueFileUploader"
                                        v-bind:filters = "filters"
                                        v-on:onAdd = "onAddItem"
                                        v-bind:events = "cbEvents"
                                        v-bind:request-options = 'datas'
                                        multiple
                                        label = '添加附件'
                                        :autoUpload = 'atuoBol'>
                                    </vue-file-upload>
                            </td>
                        </tr>
                    </tbody>
                </table>
            </div>
            <p class="parting-line" v-if="showType"></p>
            <div class="addTopicTable-footer" v-if="showType">
                <div class="header-vote">
                     <h5>{{$t('addTopic.vote.tip')}}</h5>
                 </div>
                <div v-for="(vote, index) in addTopicInfo.votes" class="Vote-Table-List">
                    <el-button class="fa fa-times fa-lg" @click="removeVote(1, index)"></el-button> 
                    <table class="vote-table">
                        <tr>
                            <th class="footer-table-td11">{{$t('addTopic.vote.title')}}</th>
                            <td colspan="2"><input type="text" class="addTopicTable-table-input" v-model="vote.title"/></td>
                        </tr>
                        <tr>
                            <th  class="footer-table-td12">{{$t('addTopic.vote.options')}}</th>
                            <td colspan="2">
                                <el-radio-group v-model="vote.type">
                                    <el-radio :label="1">{{$t('addTopic.vote.optionsTd.radio')}}</el-radio>
                                    <el-radio :label="2">{{$t('addTopic.vote.optionsTd.multiple')}}</el-radio>
                                </el-radio-group>
                            </td>
                        </tr>
                        <tr>
                            <th class="footer-table-td13" :rowspan="vote.options.length+1" >{{$t('addTopic.vote.choice')}}</th>
                            <td class="footer-table-td25" @click="addOption(vote)"><span role='button'><i class="fa fa-plus fa-lg"></i><span class="addTopicTable-table-add">{{$t('addTopic.vote.choiceTd.add')}}</span></span></td>
                        </tr>
                        <tr v-for="(option, index) in vote.options"> 
                            <td>
                                <input type="text" class="options" v-model="option.title">
                                <i class="fa fa-times fa-lg" role='button' @click="removeVote(vote.options, index)"></i>
                            </td>
                        </tr>                 
                    </table>
                </div>
                <div class="add-Vote"><span role='button' @click="addVote"><i class="fa fa-plus fa-lg"></i><span class="addTopicTable-table-add">{{$t('addTopic.vote.addVote')}}</span></span></div> 			            
            </div>
        </div>
        <!-- ================上传文件弹窗=========== -->
        <table class="file-upload-table" v-if="bol">
            <thead>
                <tr class="btn-info">
                    <th class="file-name">文件名</th>
                    <th >大小</th>
                    <th class="progress-show">进度</th>
                    <th >状态<i class="fa fa-window-close fa-2x" @click="fileCtrl()"></i></th>
                </tr>
            </thead>
            <tbody>
                <tr v-for="(file,index) in tempFiles">
                    <td>{{file.name}}</td>
                    <td>{{(file.size/1024).toFixed(2)}}kb</td>
                    <td>
                        <div class="progress">
                                <div class="progress-bar" role="progressbar" aria-valuenow="0" aria-valuemin="0"
                                    aria-valuemax="100" :style='btn(file)'>
                                {{file.progress}}%
                            </div>
                        </div>						
                    </td>
                    <td>{{onStatus(file)}}</td>
                </tr>
                <tr>
                    <td colspan="4" class="upload-all-wrap"><button @click = 'uploadAll' class='upload-all'>{{allUploadCtrl}}</button></td>
                </tr>
            </tbody>  
        </table>
        <personnel @userInfo = "userInfo" ref="userData"></personnel>
     </div>
</template>

<script>
    import VueFileUpload from 'vue-file-upload';
    import {mapActions, mapGetters} from 'vuex'
    import personnel from '../personnelList/personnelList'
    import './addTopic.scss'
    export default {
        name:'addtopic',
        data(){
            return{
                hosthostList:[],
                departmentInfo:[],
                recipients:[],
                officeInfo:[],
                officeUsers:[],
                userMsg:[],
                publisherInfo:{},
                meetingStatus:'',
                showVote:false,
                atuoBol : false,
                bol:false,
                filtersFile:false,
                allUploadCtrl : '确认上传',
		    	tempFiles : [],
		    	index : 0,
                recipientsID:[],
                editorOption: {
                	placeholder:'',
                    modules: {
			            toolbar: [
				            [
				              	'bold', 'italic', 'underline', 'strike', 'blockquote', 'code-block',
				              	{'header': 1}, {'header': 2}, {'list': 'ordered'}, {'list': 'bullet'},
								{'script': 'sub'}, {'script': 'super'}, {'indent': '-1'}, {'indent': '+1'},              	
				            	{'direction': 'rtl'}, {'align': 'center'}, 'clean', 'link', 'image'
				            ]
			            ],
			            imageResize: {
			              	displayStyles: {
			                	backgroundColor: '#FFF',
			                	border: 'none',
			                	color: 'white'
			              	}
			            }                        
                    }
                },
                cbEvents:{
                    onCompleteUpload:(file, response, status, header)=>{
                        this.addTopicInfo.attachments = this.addTopicInfo.attachments.concat(response);
                    },
                    onAddFileFail:(file)=>{
                        this.filtersFile = true;
                    }
                },
                addTopicInfo:{
                    title:'',
                    content:'',
                    type:'',
                    status:1,
                    department:'',
                    supervisor:'',
                    publisher:'',
                    attachments:[],
                    votes:[{
                        title:'',
                        type:'',
                        options:[]
                    }]
                },
                // 过滤文件格式
                filters:[
                    {
                    name:"FilterFile",
                    fn:function(file){
                        var type = '|' + file.name.slice(file.name.lastIndexOf('.') + 1) + '|';
                        return '|pdf|xls|xlsx|doc|ppt|pptx|docx|azw3|'.indexOf(type) !== -1;
                    }
                    }
                ],
                datas : {
			      	formData: {
                        // libraryId: '', 
						// uploadBy: '',
						// category: ''      		
			      	},
			      	headers: {
			      		'authorization' : 'Bearer ' + window.localStorage.getItem('token')
					},
					withCredentials : false
				}
            }    
        },
        methods:{
            allMsgScrollTop(){
                setTimeout(() =>{
                    if(this.addTopicInfo.type !== '研究类议题') return;
                    this.$refs.allMsg.scrollTo(0, 2000);
                }, 200);
            },
            TopicHandle(){
               if(this.$route.query.topicID){
                   // 编辑议题及投票
                   for(let i = 0; i < this.addTopicInfo.votes.length; i++){
                        for(let j = 0; j < this.addTopicInfo.votes[i].options.length; j++){
                            this.addTopicInfo.votes[i].options[j].index = j
                        }
                    }
                    
                   this.addTopicInfo.personnel = this.recipientsID
                   this.addTopicInfo.publisher = this.$http.GetCurrentUserInfo()._id
                    this.$http.UpdateTopic(this.$route.query.topicID, this.addTopicInfo)
                        .then(res => {
                            if(res.data.status === 3){
                                this.$router.push({name:'CompileOfficial', query:{id:this.$route.query.meetingID}})
                            }else{
                                this.$router.push({name:'TopicList', query:{meetingID:this.$route.query.meetingID}})
                            }
                        }) 
               }else{
                    if(!this.addTopicInfo.title || !this.addTopicInfo.content){
                        this.$message({
                            type: 'error',
                            message: '请将当前议题内容填写完整'
                        }) 
                        return
                    }
                   // 创建议题及投票
                    for(let i = 0; i < this.addTopicInfo.votes.length; i++){
                        for(let j = 0; j < this.addTopicInfo.votes[i].options.length; j++){
                            this.addTopicInfo.votes[i].options[j].index = j
                        }
                    }
                    this.meetingStatus === 4 ? this.addTopicInfo.status = 3 : this.addTopicInfo.status = 1;
                    this.addTopicInfo.personnel = this.recipientsID
                    this.addTopicInfo.publisher = this.$http.GetCurrentUserInfo()._id
                    this.$http.CreateTopic(this.$route.query.meetingID, this.addTopicInfo)
                        .then(res => {
                            if(this.meetingStatus === 4 || this.meetingStatus === 5) {
                                this.$router.push({name:'CompileOfficial', query:{id:this.$route.query.meetingID}})
                            }else{
                                this.$router.push({name:'TopicList', query:{meetingID:this.$route.query.meetingID}})
                            }
                            
                        })
               }
               
            },

            // 增加投票
            addVote() {
                let vote = {
                        title:'',
                        type:'',
                        options:[]
                };
                this.addTopicInfo.votes.push(vote);
                this.allMsgScrollTop()
            },
            // 增加投票选项
            addOption(vote) {
                let option = {
                        title:''
                    };
                    if (!vote.options) {
                        vote.options = []
                    }
        
                vote.options.push(option);
                this.allMsgScrollTop()
            },
            // 删除投票
            removeVote(data, index){
                data === 1 ? this.addTopicInfo.votes.splice(index, 1) : data.splice(index, 1)
            },
            userInfo(user){
                if(!this.recipients || !this.recipientsID){
                    
                    this.recipientsID = []
                    user.forEach((item) =>{
                        this.recipients.push(item.name)
                        this.recipientsID.push(item._id)       
                    })
                    return;
                }
                let names = [];
                let nameID = [];
                user.forEach((item) =>{
                    names.push(item.name)
                    nameID.push(item._id)         
                })
                for(let i = 0; i < names.length; i++){
                    this.recipients.indexOf(names[i]) === -1 ? this.recipients.push(names[i]) : 0; 
                    this.recipientsID.indexOf(nameID[i]) === -1 ? this.recipientsID.push(nameID[i]) : 0;
                }
            },

            // 删除收件人
            delUser(name, index) {
                this.recipients.splice(index, 1)
                this.recipientsID.splice(index, 1)
                this.$refs.userData.deleteUser(index)
            },

            getUserID(){
                return new Promise((resolve, reject) => {
                        this.officeInfo.forEach(item => {
                            if(item.name === this.addTopicInfo.department){
                                resolve(item._id)
                            }
                        })
                    })
                
            },

            // 选取科室获取负责人
            getSupervisor() {
                this.getUserID()
                    .then(id => {
                        this.$http.GetGroupUsers(id)
                        .then(res => {
                                this.officeUsers = res.data.list
                                if(this.officeUsers.length) return;
                                this.$message({
                                    type: 'error',
                                    message: '当前科室没有人员'
                                })             
                            })
                    })
                
            },
            
            // ==============上传文件
            onStatus(file){            	
            	if(file.isSuccess){
            		return '上传成功';
            	}else if(file.isError){
            		return '上传失败';
            	}else if(file.isUploading){
            		return '正在上传';
            	}else{
            		return '待上传';
            	}
            },
            onAddItem(files){
                if(this.filtersFile){
                    this.$notify({
                        title: '警告',
                        message: '请按照规定格式上传文件',
                        type: 'warning'
                    });
                    this.filtersFile = false
                    this.bol = false;
                    this.index = 0
                    this.$refs.vueFileUploader.fileUploader.queue = []
                    return;
                }
            	this.bol = true;
            	this.allUploadCtrl = '确认上传';
            	if(!this.index){         		
            		this.tempFiles = files
                }
                this.tempFiles = files.slice(this.index)			
            	this.index = files.length;            
            },
            btn(file){
            		return `width : ${file.progress * 1.5}px`;
            },
            fileCtrl(){
            	this.bol = false;
            },

            // 上传多个或单个
		    uploadAll(){
		    	if(this.allUploadCtrl === '上传完成') return;
				let p = Promise.resolve('ok');
				this.tempFiles.forEach(file => {
					p = p.then(res => {
						return this.uploadItem(file);
					})
				});
				p.then((data) => {
                    this.allUploadCtrl = '上传完成'
                    this.bol = false;
				})
            },
            // 上传成功就调用
		    uploadItem(file){
		    	return new Promise((resolve, reject) => {
		    		file.upload(() => {
	    				setTimeout(() => {
	    					resolve('pzz')
						}, 1000);
                    });
		    	});
            },
            
            // 删除附件
            delFile (files, index) {
                files.splice(index, 1)
            },

            // 科室&&人员
            getOfficeInfo (){
                if(!this.officeList) return
                this.officeInfo = []
                if(this.officeList.parent !== null && !this.officeList.children.length){
                    this.officeInfo.push(this.officeList)
                }else if(this.officeList.parent !== null && this.officeList.children.length){
                    this.officeInfo = this.officeList.children
                }else if(this.officeList.parent === null && !this.officeList.children.length){
                    this.officeInfo.push(this.officeList)
                }else{
                    this.officeInfo = this.officeList.children
                }
                this.getSupervisor()
            }
        },
        computed: {
            ...mapGetters([
                'sideData',
                'officeList'
            ]),
            showType(){
                
                 if(this.addTopicInfo.type === '研究类议题'){
                     this.showVote = true
                 }else{
                    this.addTopicInfo.votes = [{
                        title:'',
                        type:'',
                        options:[]
                    }]
                    this.showVote = false
                 }
                 return this.showVote
            }
        },
        components: {
            personnel,
            VueFileUpload
        },
        watch: {
            officeList(){
                this.getOfficeInfo()
            }
        },
        created(){
            if(this.$route.query.topicID){
                this.$http.GetTopic(this.$route.query.topicID)
                .then(res => {
                    if(!res.data.votes.length){
                        res.data.votes = this.addTopicInfo.votes
                    }       
                    this.addTopicInfo = res.data
                    res.data.personnel.forEach((item) =>{
                        this.recipients.push(item.name)
                        this.recipientsID.push(item._id)
                    })
                    this.getOfficeInfo()
                })
            }else{
                this.getOfficeInfo()
            }            
            this.$http.GetMeeting(this.$route.query.meetingID)
                .then(res => {
                    this.meetingStatus = res.data.status
                    this.publisherInfo = res.data.publisher
                    // 人员信息====》主讲人
                    // this.$http.GetPersonnelList()
                    //     .then(res =>{
                    //         this.hosthostList = res.data
                    //     })
                })
        }
    }
</script>


