<template>
    <div class="addRecord-page">
        <div class="addRecord-header">
            <div class="addRecord-header-back" @click="$router.back()" role='button'>
                <i class="fa fa-angle-left fa-2x"></i>
                <span>{{$t('pageCommon.back')}}</span>
			</div>
			<div class="addRecord-Handle">
				<button class="add-btn" @click="upDataRecord">{{$t('addRecord.save')}}</button>
				<button class="over-btn" @click="concludeRecord">结束</button>
			</div>
        </div>
        <div class="addRecord-body">
				<table class="addRecord-table">
					<tr>
						<td>{{$t('addRecord.title')}}</td>
						<td><input type="text" class="addRecord-table-input" v-model="recordInfo.title"/></td>
					</tr>
			   		<tr>
			   			<td>{{$t('addRecord.date')}}</td>
			   			<td>{{createdAt | filtersTime}}</td>
			   		</tr>
			   		<tr>
			   			<td>{{$t('addRecord.recorder')}}</td>
			   			<td>{{recordName}}</td>
			   		</tr>
					<tr>
			   			<td>地点</td>
			   			<td>{{location}}</td>
			   		</tr>
			   		<tr>
						<td>{{$t('addRecord.receiver')}}</td>
						<td>
							<el-tag :key="name" type="primary" v-for="(name, index) in recipients" :closable="true" :close-transition="false" @close="delUser(name, index)">
								{{name}}
							</el-tag> 
							<span role='button' @click="$refs.userData.GetPersonnelList(), $store.dispatch('personnelList')"><i class="fa fa-plus fa-lg"></i><span class="addRecord-table-add">{{$t('colTopic.selectStaff')}}</span></span>
						</td>	
			   		</tr>
			   		<!-- <tr>
			   			<td>{{$t('addRecord.upload')}}</td>
			   			<td><span role='button'><i class="fa fa-paperclip fa-lg"></i><span class="addRecord-table-add">{{$t('addRecord.uploadTd.add')}}</span></span></td>
			   		</tr> -->
			   		<tr>
			   			<td>{{$t('addRecord.record')}}</td>
			   			<td class="editor-td"><quill-editor class="addRecordTable-table-content" v-model="recordInfo.content"></quill-editor></td>
			   		</tr>			   		
				</table>
		</div>
		<personnel @userInfo = "userInfo" ref="userData"></personnel>
	</div>	
</template>

<script>
import './addRecord.scss'
import personnel from '../personnelList/personnelList'
export default {
	name : 'AddRecord',
	data(){
		return {
			recipients:[],
			recipientsID:[],
			recordID:'',
			recordName:'',
			createdAt:'',
			location:'',
			recordInfo:{
				title:'',
				personnel:[],
				recorder:'',
				content:''
			}
		}
	},
	methods: {
		// 更新保存会议纪要
		upDataRecord(){
			this.recordInfo.personnel = this.recipientsID
			this.$http.UpdataRecords(this.recordID, this.recordInfo)
		},
		// 结束编辑会议纪要
		concludeRecord(){
			this.$http.ConcludeRecord(this.recordID, 1)
				.then(res =>{
					this.$router.push({name:'Conference'})
				})
		},
		// 处理收件人
		userInfo(user){
			if(!this.recipients || !this.recipientsID){
				user.forEach((item) =>{
					this.recipients.push(item.name)
					this.recipientsID.push(item._id)       
				})
				return;
			}
			let names = [];
			let nameID = [];
			user.forEach((item) =>{
				names.push(item.name)
				nameID.push(item._id)         
			})
			for(let i = 0; i < names.length; i++){
				this.recipients.indexOf(names[i]) === -1 ? this.recipients.push(names[i]) : 0; 
				this.recipientsID.indexOf(nameID[i]) === -1 ? this.recipientsID.push(nameID[i]) : 0;
			}
		},
		// 删除收件人
		delUser(name, index) {
			this.recipients.splice(index, 1)
			this.recipientsID.splice(index, 1)
		}
	},
	components: {
		personnel
	},
	created(){
		this.$http.GetMeetingRecords(this.$route.query.id)
			.then(res =>{
				this.location = res.data.location
				this.createdAt = res.data.createdAt
				this.recordInfo = res.data
				this.recordID = res.data._id
				this.recordName = this.$http.GetCurrentUserInfo().name
				this.recordInfo.recorder = this.$http.GetCurrentUserInfo()._id
				res.data.personnel.forEach((item) =>{
					this.recipients.push(item.name)
					this.recipientsID.push(item._id)  
				})

			})
	}

}
</script>


